/* quicksand-300normal - latin */
@font-face {
  font-family: 'Quicksand';
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src:
    local('Quicksand Light '),
    local('Quicksand-Light'),
    url(/static/media/quicksand-latin-300.ca35298d.woff2) format('woff2'), 
    url(/static/media/quicksand-latin-300.253b7a2b.woff) format('woff'); /* Modern Browsers */
}

